import React, { useRef, useState } from "react";
import OverlayTrigger from "react-bootstrap/OverlayTrigger";
import Popover from "react-bootstrap/Popover";
import { Button, Col, Container, Image, Row } from "react-bootstrap";
import DialogoCompra from "./DialogoCompra";
import DialogoAviso from "./DialogoAviso";
import DIAG_TIPO from "../meta/Valores";
import { moneda } from "../misc/CalcUtils";
import { ColoresManager } from "../meta/ColoresManager";
import SizeAnimation from "../tools/SizeAnimation";
import DialogoComodin from "./DialogoComodin";

const PanelJuego = ({ sendAction, partida, idJugador }) => {
  const [showCreditList, setShowCreditList] = useState(false);
  const colores = ColoresManager();
  function handleQuitGame() {
    sendAction("quit", "Deseo salir");
    console.log("saliendo del juego");
  }
  function handleFinishTurn() {
    sendAction("finishTurn");
  }
  function handleHardRoll(e) {
    if (idJugador == partida.jugadorActual && partida.estado == "J") {
      const val = parseInt(e.target.innerText);
      sendAction("rollDice", val);
    }
  }
  const dValores = [0, 1, 2, 3, 4, 5, 6, 7];
  //solo leer la primera ventana que ser la de mayor prioridad.
  const dg = partida.dialogos[0];
  const jActual = partida.jugadores.find((j) => {
    return j.id == partida.jugadorActual;
  });
  const jLocal = partida.jugadores.find((j) => {
    return j.id == idJugador;
  });
  function mostrarDialogo(tipo) {
    if (!tipo) return null;
    const manage = idJugador == partida.jugadorActual;
    switch (tipo) {
      case DIAG_TIPO.COMPRAR_TITULO:
        return (
          <DialogoCompra
            manage={manage}
            partida={partida}
            data={dg}
            sendAction={sendAction}
            solvente={jActual.efectivo >= dg.contenido.precio}
          />
        );
      case DIAG_TIPO.DECLARAR_BANCAROTA:
        let mensaje = `@j${dg.contenido.iddeudor} tiene una deuda superior a su efectivo disponible y est evaluando si debe declararse en BANCA ROTA`;
        if (dg.contenido.iddeudor == idJugador)
          mensaje = `@j${idJugador} ,  tienes una deuda superior a tu efectivo disponible Deseas declararte en BANCA ROTA?`;
        return (
          <DialogoAviso
            partida={partida}
            manage={manage}
            idd={dg.id}
            mensaje={mensaje}
            sendAction={sendAction}
          />
        );
      case DIAG_TIPO.PAGO_JUGADOR:
      case DIAG_TIPO.GANADOR:
      case DIAG_TIPO.FERIADO:
      case DIAG_TIPO.DESCANSO:
      case DIAG_TIPO.SALDO_INSUFICIENTE:
        return (
          <DialogoAviso
            partida={partida}
            manage={manage}
            idd={dg.id}
            mensaje={dg.contenido.texto}
            sendAction={sendAction}
            ok={tipo != DIAG_TIPO.GANADOR}
          />
        );
      case DIAG_TIPO.COMODIN:
        return (
          <DialogoComodin
            partida={partida}
            manage={manage}
            data={dg}
            sendAction={sendAction}
          />
        );
      default:
        return null;
    }
  }
  function limiteCredito() {
    let total = 0;
    jLocal.pagares.forEach((p, i) => {
      if (p) total += (i + 1) * 10;
    });
    return total;
  }
  function handleCobrarPagare(e) {
    //creditPaneRef.current.show = false;
    //console.log(creditPaneRef.current);
    setShowCreditList(false);
    const creditId = e.target.id - 1;
    //console.log(creditId);
    sendAction("takeCredit", creditId);
  }
  return (
    <Container
      className="px-0 d-flex flex-column h-100"
      style={{ height: "100%" }}
    >
      <Row style={{ height: "5%" }} />
      <Row className="justify-content-center flex-grow-1">
        <Col className="col-2"></Col>
        <Col>
          <Row style={{ height: "50%" }}>{mostrarDialogo(dg?.tipo)}</Row>
          <Row style={{ height: "50%" }} />
        </Col>
        <Col className="col-2" style={{ paddingLeft: 0 }}>
          <Row className="my-2" style={{ height: "60px" }}>
            {partida.estado != "R" &&
              partida.d1Ix != undefined &&
              partida.d2Ix != undefined && (
                <div>
                  <Image
                    className="px-0"
                    src={`/imagenes/dado1_${partida.d1Ix}.gif`}
                    style={{
                      position: "fixed",
                      display: "block",
                      width: "60px",
                    }}
                  />
                  <Image
                    className="px-0"
                    src={`/imagenes/dado2_${partida.d2Ix}.gif`}
                    style={{
                      position: "fixed",
                      display: "block",
                      width: "60px",
                    }}
                  />
                </div>
              )}
          </Row>
          <Row>
            {import.meta.env.DEV &&
              partida.estado == "J" &&
              idJugador == partida.jugadorActual &&
              dValores.map((v) => {
                return (
                  <Button
                    key={v}
                    style={{
                      width: "30px",
                      height: "30px",
                      pointerEvents: "all",
                    }}
                    onClick={handleHardRoll}
                  >
                    {v}
                  </Button>
                );
              })}
          </Row>
          <Row style={{ justifyContent: "center" }} className="my-2"></Row>
        </Col>
      </Row>
      {partida.estado != "R" && (
        <Row
          style={{
            height: "fit-content",
            pointerEvents: "all",
            margin: "5px",
            padding: "5px",
            backgroundColor: "rgba(50, 50, 50, 0.5)",
            borderRadius: "10px",
            backdropFilter: "blur(5px)",
            marginBottom: "15px",
          }}
        >
          <Col>
            <Row
              style={{
                justifyContent: "center",
                fontWeight: "bold",
                color: colores[jLocal.colorId].valor,
                fontSize: "1.5em",
                marginBottom: "5px",
                filter: "drop-shadow(1px 1px 1px rgba(0, 0, 0, 1))",
              }}
            >
              <Col style={{ padding: 0, textAlign: "right" }}>
                {["Z", "S"].includes(partida.estado) &&
                  idJugador == partida.jugadorActual && (
                    <SizeAnimation>
                      <Image
                        src="/imagenes/finishTurn.png"
                        title="Terminar"
                        onClick={handleFinishTurn}
                        style={{
                          cursor: "pointer",
                          padding: 0,
                          filter: "drop-shadow(1px 1px 1px rgba(0, 0, 0, 1))",
                          width: "100%",
                          height: "100%",
                        }}
                      />
                    </SizeAnimation>
                  )}
              </Col>
              <Col xs="auto">{jLocal.nombre}</Col>
              <Col style={{ padding: 0 }}></Col>
            </Row>
            <Row>
              <Col>
                <Image
                  src="/imagenes/wallet.png"
                  title="Efectivo"
                  style={{
                    padding: 0,
                    width: "32px",
                    height: "32px",
                    filter: "drop-shadow(2px 2px 1px rgba(0, 0, 0, 1))",
                  }}
                />
                <p
                  style={{
                    color: jLocal.efectivo > 0 ? "#35cf16" : "red",
                    fontWeight: "bold",
                    filter: "drop-shadow(1px 1px 1px rgba(0, 0, 0, 1))",
                  }}
                >
                  {moneda(jLocal.efectivo)}
                </p>
              </Col>
              <Col>
                <OverlayTrigger
                  onToggle={(p) => {
                    setShowCreditList((prev) => !prev && limiteCredito() > 0);
                  }}
                  show={showCreditList}
                  rootClose
                  rootCloseEvent="mousedown"
                  trigger="click"
                  placement="top"
                  overlay={
                    <Popover
                      style={{
                        width: "min-content",
                        backgroundColor: "#222222",
                      }}
                    >
                      <Popover.Body>
                        {jLocal.pagares.map((p, i) => {
                          if (!p) return null;
                          return (
                            <div
                              key={i + 1}
                              id={i + 1}
                              size="sm"
                              style={{
                                marginBottom: "5px",
                                fontSize: "11px",
                                color: "#22",
                                backgroundColor: "#00ffff",
                                padding: "5px",
                                fontWeight: "bold",
                                borderRadius: "3px",
                              }}
                              onClick={handleCobrarPagare}
                            >
                              {moneda((i + 1) * 10)}
                            </div>
                          );
                        })}
                      </Popover.Body>
                    </Popover>
                  }
                >
                  <Image
                    src="/imagenes/credit.png"
                    title="Crdito"
                    style={{
                      padding: 0,
                      width: "32px",
                      height: "32px",
                      filter: "drop-shadow(2px 2px 1px rgba(0, 0, 0, 1))",
                    }}
                  />
                </OverlayTrigger>
                <p
                  style={{
                    color: "#a7ff36",
                    fontWeight: "bold",
                    fontSize: "12px",
                    filter: "drop-shadow(1px 1px 1px rgba(0, 0, 0, 1))",
                    marginBottom: "1px",
                  }}
                >
                  {moneda(limiteCredito())}
                </p>
                {jLocal.deuda > 0 && (
                  <p
                    style={{
                      color: "red",
                      fontWeight: "bold",
                      fontSize: "12px",
                      filter: "drop-shadow(1px 1px 1px rgba(0, 0, 0, 1))",
                      marginBottom: "1px",
                    }}
                  >
                    {`(${moneda(jLocal.deuda)})`}
                  </p>
                )}
              </Col>
              <Col>
                <div
                  style={{
                    width: "100%",
                    height: "32px",
                  }}
                >
                  <Image
                    src="/imagenes/sueldo.png"
                    title="Tarjeta de sueldo"
                    style={{
                      padding: 0,
                      width: "32px",
                      filter: "drop-shadow(1px 1px 1px rgba(0, 0, 0, 1))",
                    }}
                  />
                </div>
                <p
                  style={{
                    color: "darkgray",
                    fontWeight: "bold",
                    filter: "drop-shadow(1px 1px 1px rgba(0, 0, 0, 1))",
                  }}
                >
                  {jLocal.numTarjSueldo}
                </p>
              </Col>
              <Col>
                <Image
                  src="/imagenes/quitgame.png"
                  title="Dejar partida"
                  onClick={handleQuitGame}
                  style={{
                    cursor: "pointer",
                    padding: 0,
                    width: "32px",
                    height: "32px",
                    filter: "drop-shadow(2px 2px 1px rgba(0, 0, 0, 1))",
                  }}
                />
              </Col>
            </Row>
          </Col>
        </Row>
      )}
    </Container>
  );
};

export default PanelJuego;
