import React, { useState, useEffect } from "react";

const SizeAnimation = (props) => {
  const min = 18;
  const max = 26;
  const tiempo = 3000; //milisegundos
  const [valor, setValor] = useState(min);
  const [isGrowing, setIsGrowing] = useState(1);
  useEffect(() => {
    const intervalId = setInterval(() => {
      setValor((prev) => {
        return Math.round((valor + 1 * isGrowing) * 10) / 10;
      });
      if (
        (valor <= min && isGrowing == -1) ||
        (valor >= max && isGrowing == 1)
      ) {
        setIsGrowing((prev) => {
          return isGrowing * -1;
        });
      }
    }, 50);

    return () => clearInterval(intervalId);
  }, [valor, isGrowing]);

  return (
    <div
      style={{
        width: valor,
        height: valor,
        textAlign: "right",
        display: "inline-flex",
      }}
    >
      {props.children}
    </div>
  );
};

export default SizeAnimation;
