import React from "react";
import { Button, Container, Image, Row } from "react-bootstrap";

const DialogoComodin = ({ partida, manage, data, sendAction }) => {
  function handleAceptar() {
    sendAction("closeDialog", { idg: data.id });
  }
  function findImage(idcomodin) {
    const espImgs = [];
    espImgs[2] = "c2_10_16_20_28_47_49_61.png";
    espImgs[10] = "c2_10_16_20_28_47_49_61.png";
    espImgs[16] = "c2_10_16_20_28_47_49_61.png";
    espImgs[20] = "c2_10_16_20_28_47_49_61.png";
    espImgs[28] = "c2_10_16_20_28_47_49_61.png";
    espImgs[47] = "c2_10_16_20_28_47_49_61.png";
    espImgs[49] = "c2_10_16_20_28_47_49_61.png";
    espImgs[61] = "c2_10_16_20_28_47_49_61.png";
    espImgs[4] = "c4_32.png";
    espImgs[32] = "c4_32.png";
    espImgs[35] = "c35_71.png";
    espImgs[71] = "c35_71.png";
    if (espImgs[idcomodin]) {
      return `/imagenes/${espImgs[idcomodin]}`;
    }
    return `/imagenes/c${idcomodin}.png`;
  }
  return (
    <Container
      fluid="md"
      style={{
        pointerEvents: "all",
        height: "fit-content",
        backgroundColor: "rgba(50, 50, 50, 0.5)",
        padding: "5px",
        borderRadius: "5px",
        backdropFilter: "blur(5px)",
      }}
    >
      <Row xs="auto" className="justify-content-center">
        <Image src={findImage(data.contenido.id)}></Image>
      </Row>
      <Row>
        <p style={{ color: "darkgrey" }}>{data.contenido.texto}</p>
      </Row>
      <Row className="my-1" style={{ justifyContent: "center" }}>
        {manage && (
          <Button
            size="sm"
            onClick={handleAceptar}
            style={{ width: "fit-content" }}
          >
            OK
          </Button>
        )}
      </Row>
    </Container>
  );
};

export default DialogoComodin;
