import React from "react";
import { Col, Image } from "react-bootstrap";

const Exit = ({ onExit, Nombre }) => {
  function handleExit(e) {
    onExit();
  }
  return (
    <>
      <Col>
        <h1 className="form-label" style={{ color: "aqua", textAlign: "left" }}>
          Investor ML
        </h1>
      </Col>
      <Col
        className="mt-2 justify-content-end"
        style={{
          display: "flex",
          verticalAlign: "middle",
        }}
      >
        <div style={{ color: "white" }}>Hola {Nombre}</div>
        <Image
          src="/imagenes/exit.png"
          title="salir"
          onClick={handleExit}
          style={{
            cursor: "pointer",
            padding: 0,
            width: "24px",
            height: "24px",
            marginLeft: "5px",
          }}
        />
      </Col>
    </>
  );
};

export default Exit;
