import React, { useRef, useState } from "react";
import {
  Badge,
  Button,
  Col,
  Container,
  Form,
  ListGroup,
  Row,
} from "react-bootstrap";

const SelectGame = ({ partidaNomDef, hPartidas, sendAction }) => {
  const partidaRef = useRef();
  const [partidas, setPartidas] = hPartidas;

  function handleSubmit(e) {
    e.preventDefault();
    e.stopPropagation();
    var nombrePartida = partidaRef.current.value;
    sendAction("createGame", nombrePartida);
  }

  function handleItemClick(e) {
    let idPartida = e.target.value;
    console.log("Se unir  a la partida " + idPartida);
    sendAction("join", idPartida);
  }

  return (
    <Container className="mt-4">
      <Form.Label style={{ color: "aqua" }}>Selecciona una partida</Form.Label>
      <ListGroup
        className="overflow-auto"
        style={{
          minHeight: "300px",
          maxHeight: "200px",
          padding: 0,
          backgroundColor: "rgba(0, 0, 0, 0.5)",
          border: "solid 1px white",
          borderRadius: "0px",
        }}
      >
        {partidas &&
          partidas.map(function (prt) {
            return (
              <ListGroup.Item
                key={prt.id}
                value={prt.id}
                type="button"
                action
                className="d-flex justify-content-between align-items-center"
                onClick={handleItemClick}
              >
                {prt.nombre}
                <Badge bg="primary" pill style={{ backgroundColor: "#0d6efd" }}>
                  {" (" + prt.pop + ")"}
                </Badge>
              </ListGroup.Item>
            );
          })}
      </ListGroup>
      <Form onSubmit={handleSubmit}>
        <Form.Label style={{ color: "aqua" }}>
          o crea tu propia partida
        </Form.Label>
        <Row>
          <Col xs={9}>
            <Form.Control
              defaultValue={partidaNomDef}
              type="text"
              ref={partidaRef}
            />
          </Col>
          <Col xs={3}>
            <Button type="submit">Crear</Button>
          </Col>
        </Row>
      </Form>
    </Container>
  );
};

export default SelectGame;
