import React from "react";
import { Fade } from "react-bootstrap";
import Alert from "react-bootstrap/Alert";

function Alerta({ hMensajeErr }) {
  const [mensajeErr, setMensajeErr] = hMensajeErr;

  if (mensajeErr) {
    setTimeout(function () {
      setMensajeErr("");
    }, 3000);
    return (
      <Alert
        variant="warning"
        onClose={() => setMensajeErr("")}
        dismissible
        transition={Fade}
      >
        <p> {mensajeErr} </p>
      </Alert>
    );
  }
}

export default Alerta;
