import React, { useState } from "react";
import { Button, Col, Container, Row } from "react-bootstrap";
import { Canvas, useLoader } from "@react-three/fiber";
import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader";
import FichasModelos from "./FichasModelos";
import { FichasManager } from "../meta/FichasManager";
import { ColoresManager } from "../meta/ColoresManager";

export const SelectFicha = ({ setShow, colorIni, fichaIni, sendAction }) => {
  const colores = ColoresManager();
  const fichas = FichasManager();
  fichas.forEach((f) => {
    const gltf = useLoader(GLTFLoader, f.ruta);
    f.ficha3D = gltf;
  });

  const [colorSt, setColorSt] = useState(colores[colorIni]);
  const [fichaISt, setFichaISt] = useState(
    fichas.find((f) => f.nombre == fichaIni).id
  );

  function handleSeleccionar() {
    setShow(false);
    console.log(
      `se ha seleccionado la ficha ${fichas[fichaISt].nombre} de color ${colorSt.nombre}`
    );
    sendAction("changeChar", {
      colorId: colorSt.id,
      fichaNom: fichas[fichaISt].nombre,
    });
  }

  function handleSelColor(e) {
    let clid = e.target.id;
    setColorSt(colores[clid]);
  }

  function handleFichaDer() {
    if (fichaISt + 1 > fichas.length - 1) {
      setFichaISt(0);
    } else {
      setFichaISt((prev) => prev + 1);
    }
  }

  function handleFichaIzq() {
    if (fichaISt - 1 < 0) {
      setFichaISt(fichas.length - 1);
    } else {
      setFichaISt((prev) => prev - 1);
    }
  }

  return (
    <div style={{ width: "100%", height: "100%", position: "relative" }}>
      <div
        style={{
          width: "100%",
          position: "absolute",
          zIndex: 1,
        }}
      >
        <Row className="align-items-center">
          <Col>
            <Button onClick={handleFichaIzq}>{"<"}</Button>
          </Col>
          <Col>
            <div style={{ width: 140, height: 400 }} />
          </Col>
          <Col>
            <Button onClick={handleFichaDer}>{">"}</Button>
          </Col>
        </Row>
        <Row>
          <Col style={{ height: 40 }}>
            <Container style={{ height: "100%" }}>
              <Row style={{ height: "100%" }}>
                <Col></Col>
                {colores.map((c) => {
                  return (
                    <Col
                      key={c.nombre}
                      as="button"
                      xs={1}
                      id={c.id}
                      style={{
                        backgroundColor: c.valor,
                        borderRadius: 7,
                      }}
                      title={c.nombre}
                      onClick={handleSelColor}
                    />
                  );
                })}
                <Col></Col>
              </Row>
            </Container>
          </Col>
        </Row>
        <Row>
          <Col>
            <Button style={{ marginBlock: 10 }} onClick={handleSeleccionar}>
              Seleccionar
            </Button>
          </Col>
        </Row>
      </div>
      <div
        style={{
          width: "100%",
          height: "50vh",
        }}
      >
        <Canvas
          camera={{
            fov: 75,
            near: 0.1,
            far: 1000,
            position: [0, 15, 50],
          }}
        >
          <ambientLight intensity={2} />
          <directionalLight intensity={2} color={"white"} />
          <FichasModelos
            color={colorSt.valor}
            ficha3D={fichas[fichaISt].ficha3D}
            escena={1}
          />
        </Canvas>
      </div>
    </div>
  );
};
