import React, { useRef } from "react";
import { Button, Container, Form, Row, Col, Stack } from "react-bootstrap";
import Alerta from "./Alerta";

const Login = ({ sendAction, hMensajeErr, handshakeMessage }) => {
  const usernameRef = useRef();
  const [mensajeErr, setMensajeErr] = hMensajeErr;

  function handleSubmit(e) {
    e.preventDefault();
    e.stopPropagation();
    sendAction("login", usernameRef.current.value);
  }

  return (
    <div style={{ pointerEvents: "all" }}>
      <Row style={{ height: 32 }} />
      <div className="container" style={{ maxWidth: "500px" }}>
        <div className="mt-4" style={{ color: "aqua" }}>
          <h1 className="form-label">Investor ML</h1>
          <p>El siguiente millonario puedes ser t&uacute;</p>
        </div>
        <div className="logo">
          <img src="imagenes/logo_64.png" loading="lazy" alt="" />
        </div>
        <Row>
          <Col className="d-flex justify-content-center">
            <Container
              className="align-items-center d-flex"
              style={{ height: "40vh" }}
            >
              <Form onSubmit={handleSubmit} id="frm01" className="w-100">
                <Form.Group>
                  <Form.Label style={{ color: "aqua" }}>
                    Ingrese su nombre
                  </Form.Label>
                  <Form.Control type="text" ref={usernameRef} required />
                </Form.Group>
                <Button type="submit" id="conectar" className="mt-2">
                  Conectar
                </Button>
                {!handshakeMessage.startsWith("0") ? (
                  <p style={{ color: "aqua" }}>{handshakeMessage}</p>
                ) : null}
              </Form>
            </Container>
          </Col>
        </Row>
      </div>
      <Alerta hMensajeErr={[mensajeErr, setMensajeErr]} />
    </div>
  );
};

export default Login;
