import { useFrame } from "@react-three/fiber";
import React, { useMemo, useRef } from "react";
import * as THREE from "three";

const FichasJugador = ({
  ficha3D,
  fichaEstado,
  fichaTransform,
  orbitaRef,
  onSeleccionar,
}) => {
  const fichaEscena = useRef();
  let mixer = useMemo(() => {
    const mixer = new THREE.AnimationMixer(ficha3D.scene);
    if (ficha3D.animations.length) {
      let clip = ficha3D.animations[fichaEstado];
      const action = mixer.clipAction(clip);
      action.setDuration(0.8);
      action.play();
    }
    return mixer;
  }, [ficha3D, fichaEstado]);
  const m4 = new THREE.Matrix4();
  const fase = 0.2;
  let pos = new THREE.Vector3();
  let qat = new THREE.Quaternion();
  let scl = new THREE.Vector3();
  let pos1 = new THREE.Vector3();
  let qat1 = new THREE.Quaternion();
  let scl1 = new THREE.Vector3();
  useFrame((_, delta) => {
    //actualizar posicin
    ficha3D.scene.matrixAutoUpdate = false;
    if (fichaTransform) m4.fromArray(fichaTransform);
    if (fichaEstado == 0) {
      // solo cuando el ficha est caminando
      ficha3D.scene.matrix.decompose(pos, qat, scl);
      //inicializar matriz recibida
      m4.decompose(pos1, qat1, scl1);
      //obtener distancia
      const distancia = pos1.distanceTo(pos);
      //obtener la proporcin de la fase en la distancia calculada
      if (distancia > 0 && fase < distancia) {
        const alfa = fase / distancia;
        pos.lerp(pos1, alfa);
      } else {
        pos.copy(pos1);
      }
      ficha3D.scene.matrix.compose(pos, qat1, scl1);
      //posicionar camara detras de persona.
      orbitaRef.current.setTarget(pos.x, 0, pos.z, true);
      orbitaRef.current.setPosition(pos.x, 50, pos.z + 50, true);
    } else {
      ficha3D.scene.matrix.copy(m4);
    }
    //actualizar animacin
    mixer?.update(delta);
  });

  return (
    <primitive
      ref={fichaEscena}
      object={ficha3D.scene}
      onClick={onSeleccionar}
    />
  );
};

export default FichasJugador;
