import React, { useState, useEffect, useRef } from "react";
import Login from "./Login";
import useLocalStorage from "../hooks/useLocalStorage";
import GameSettings from "./GameSettings";
import useWebSocket, { ReadyState } from "react-use-websocket";
import PanelJuego from "./PanelJuego";
import { FichasManager } from "../meta/FichasManager";
import { ColoresManager } from "../meta/ColoresManager";
import { GameContext } from "../contexts/GameContext";
import GameMain from "./GameMain";
import NoSleep from "nosleep.js";

function App() {
  var nosleep = new NoSleep();
  nosleep.enable();
  const [username, setUsername] = useLocalStorage("username", "");
  const [token, setToken] = useLocalStorage("token", "");
  const [idJugador, setIdJugador] = useLocalStorage("IdJugador", 0);
  const [mensajeErr, setMensajeErr] = useState();
  const [mensajes, setMensajes] = useState();
  const [partidas, setPartidas] = useState();
  const [partida, setPartida] = useState();
  /**
   * activa la vista por defecto
   */
  const [activateDefaultView, setActivateDefaultView] = useState(false);
  /**
   * 0: no iniciado, 1: animando, 2: finalizado
   */
  const [egi, setEgi] = useLocalStorage("egi", 0);
  const [handshakeMessage, setHandshakeMessage] = useState("");
  const [selectFicha, setSelectFicha] = useState();
  const fichas = FichasManager();
  const colores = ColoresManager();

  const loc = window.location;
  const wsurlPRD = `ws${loc.protocol == "http:" ? "" : "s"}://${loc.host}`;
  const wsurlDEV = `ws://${loc.hostname}:3000`;
  const wsurl = import.meta.env.DEV ? wsurlDEV : wsurlPRD;

  const { sendJsonMessage, lastJsonMessage, readyState } = useWebSocket(wsurl, {
    queryParams: { token: token },
    onError: handleError,
    share: true,
    //shouldReconnect: (closeEvent) => true,
  });
  //solo ejecutar una vez
  // useEffect(() => {

  //   //window.scrollTo(0, 1);
  // }, []);
  //revisar ESTADO DE CONEXIN
  useEffect(() => {
    if (readyState === ReadyState.OPEN) {
      console.log("conexin abierta!");
      setMensajes("");
    } else if (readyState === ReadyState.CLOSED) {
      limpiarConexion();
      console.log("conexin cerrada!");
    }
  }, [readyState]);

  //revisar MENSAJES DE SOCKET
  useEffect(() => {
    if (lastJsonMessage) {
      let rsp = lastJsonMessage;
      console.log(`El mensaje recibido es: ${JSON.stringify(rsp)}`);
      switch (rsp.type) {
        case "loggedin":
          let j = rsp.content;
          setToken(j.token);
          setIdJugador(j.id);
          setUsername(j.nombre);
          break;
        case "games":
          setPartidas(rsp.content);
          setPartida(null);
          setEgi(0);
          break;
        case "game":
          if (rsp.content.partida) {
            setPartida(rsp.content.partida);
          } else if (rsp.content.jugador) {
            const jd = rsp.content.jugador;
            const jugadoresCopy = Array.from(partida.jugadores);
            let indice = -1;
            jugadoresCopy.every((j, index) => {
              if (j.id == jd.id) {
                indice = index;
                return false;
              }
              return true;
            });
            if (indice >= 0) {
              jugadoresCopy[indice] = rsp.content.jugador;
              setPartida({ ...partida, jugadores: jugadoresCopy });
            } else {
              console.log(`No se ha encontrado el jugador local ${jd.id}`);
            }
          } else {
            console.log("No se encontr un objeto");
          }
          if (rsp.content.msj && rsp.content.msj != "")
            setMensajes(`${mensajes ? mensajes : ""}${rsp.content.msj}\n`);
          break;
        case "error":
          console.log(rsp.content);
          setMensajeErr(rsp.content);
          break;
        case "message":
          if (rsp.content != "")
            setMensajes(`${mensajes ? mensajes : ""}${rsp.content}\n`);
          break;
        case "handshake":
          //mensaje inicial para no iniciados
          let plural = rsp.content == 1 ? "" : "s";
          setHandshakeMessage(
            `${rsp.content} usuario${plural} conectado${plural}`
          );
          break;
      }
    }
  }, [lastJsonMessage]);

  //revisar CAMBIOS DE ESTADO
  useEffect(() => {
    if (!partida) return;
    if (partida.estado == "N") {
      setMensajes("");
    }
    if (partida.estado == "J") {
      if (partida.jugadorActual == idJugador) {
        console.log("es tu turno");
      }
    }

    console.log(`el nuevo estado es : ${partida.estado}`);
  }, [partida?.estado]);

  function limpiarConexion() {
    setToken("");
    setUsername("");
    setIdJugador(0);
    setEgi(0);
  }
  /**
   * envia accin al servidor
   * @tipo el codigo de acccin
   * @contenido el contenido en formato json
   */
  function sendAction(tipo, contenido) {
    const msg = {
      type: tipo,
      content: contenido,
      token: token,
      date: Date.now(),
    };
    sendJsonMessage(msg);
  }

  function handleError(evt) {
    console.log("Se ha detectado un error en la conexin");
  }

  return (
    <div
      style={{
        width: "100%",
        height: "100%",
        position: "relative",
        pointerEvents: "none",
      }}
    >
      {partida?.estado == "N" ? (
        <div
          style={{
            opacity: 0.5,
            backgroundColor: "#000",
            height: "100vh",
            width: "100vw",
            top: 0,
            left: 0,
            position: "fixed",
            zIndex: 2,
            color: "aqua",
            fontWeight: "bold",
            textAlign: "center",
            verticalAlign: "middle",
            fontSize: "xLarge",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
          }}
        >
          Iniciando partida...
        </div>
      ) : null}
      <div
        className="container1"
        style={{
          width: "100%",
          position: "absolute",
          zIndex: 1,
        }}
      >
        {username ? (
          !partida || partida.estado == "I" ? (
            <GameSettings
              userInfo={[username, token, idJugador]}
              hMensajes={[mensajes, setMensajes]}
              sendAction={sendAction}
              hMensajeErr={[mensajeErr, setMensajeErr]}
              hPartidas={[partidas, setPartidas]}
              partida={partida}
              hSelectFicha={[selectFicha, setSelectFicha]}
              fichas={fichas}
              colores={colores}
            />
          ) : (
            <PanelJuego
              sendAction={sendAction}
              setActivateDefaultView={setActivateDefaultView}
              partida={partida}
              idJugador={idJugador}
            />
          )
        ) : (
          <Login
            sendAction={sendAction}
            hMensajeErr={[mensajeErr, setMensajeErr]}
            handshakeMessage={handshakeMessage}
          />
        )}
      </div>
      <GameContext.Provider
        value={{
          partida,
          fichas,
          colores,
          idJugador,
          egi,
          setEgi,
          sendAction,
        }}
      >
        <GameMain
          username={username}
          sendAction={sendAction}
          selectFicha={selectFicha}
        />
      </GameContext.Provider>
    </div>
  );
}

export default App;
