import { useLoader } from "@react-three/fiber";
import React, { useRef } from "react";
import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader.js";

const Tablero = ({ partida, sendAction, idJugador, onDoubleCick }) => {
  const gltf = useLoader(GLTFLoader, "/modelos/tableroShow.gltf");
  const casillasRefs = Array.from({ length: 73 }, () => useRef(null));

  function handleClick(e) {
    const obj = e.object;
    const [nombre, pos] = obj.name.split("_");
    if (pos && partida?.tablero) {
      const posicion = parseInt(pos);
      const casilleros = partida.tablero.casilleros;
      if (idJugador == partida.jugadorActual && casilleros[posicion].elegible) {
        sendAction("selectSite", posicion);
      }
    }
  }
  let [clickCount, x, y, marca] = [0, 0, 0, 0];
  function handleGroupClick(e) {
    const [thDistancia, thTiempo] = [10, 200];
    const [diffX, diffY, diffT] = [
      Math.abs(e.screenX - x),
      Math.abs(e.screenY - y),
      Math.abs(e.timeStamp - marca),
    ];
    if (diffT > thTiempo) clickCount = 0;
    clickCount++;
    if (
      clickCount == 2 &&
      diffX <= thDistancia &&
      diffY <= thDistancia &&
      diffT <= thTiempo
    ) {
      clickCount = 0;
      onDoubleCick(e);
    }
    x = e.screenX;
    y = e.screenY;
    marca = e.timeStamp;
  }

  const tablero = useRef();

  return (
    <group>
      <primitive
        object={gltf.nodes.tablero}
        ref={tablero}
        receiveShadow={true}
        onClick={handleGroupClick}
      />
      {Object.getOwnPropertyNames(gltf.nodes).map((objName) => {
        const [nombre, posicion] = objName.split("_");
        if (posicion) {
          const index = parseInt(posicion);
          const obj = gltf.nodes[objName];
          const casTab = partida?.tablero?.casilleros[index];
          return (
            <primitive
              key={index}
              object={obj}
              onClick={handleClick}
              ref={casillasRefs[index]}
            >
              <meshBasicMaterial
                color={"#0C0C0C"}
                opacity={casTab ? 1 - casTab.transparencia : 0}
                transparent={true}
                attach="material"
              />
            </primitive>
          );
        }
      })}
    </group>
  );
};

export default Tablero;
