import React from "react";
//const reactStringReplace = require('react-string-replace');
import { ColoresManager } from "../meta/ColoresManager";
import { moneda } from "../misc/CalcUtils";
const Formato = ({ partida, mensaje }) => {
  const colores = ColoresManager();
  function formatear() {
    const regexp = /\@(j\d+|d\d+(\.\d+)*)/g;
    const matches = [...mensaje.matchAll(regexp)];
    const cambios = [];
    matches.forEach((matcher) => {
      const placeholder = matcher[0];
      const clave = matcher[1];
      const clase = clave.at(0);
      switch (clase) {
        case "j":
          const idjugador = parseInt(clave.substring(1));
          const ja = partida.jugadores.find((j) => {
            return j.id == idjugador;
          });
          const colorHex = colores[ja.colorId].valor;
          cambios[placeholder] = (
            <span style={{ color: colorHex, fontWeight: "bold" }}>
              {ja.nombre}
            </span>
          );
          break;
        case "d":
          const valor = parseFloat(clave.substring(1));
          const valorStr = moneda(valor);
          cambios[placeholder] = (
            <span style={{ color: "#CCFFCC" }}>{valorStr}</span>
          );
          break;
      }
    });

    const palabras = mensaje.split(" ");

    return (
      <div
        style={{
          marginBottom: "10px",
          color: "#dcdcdc",
          display: "flex",
          flexWrap: "wrap",
        }}
      >
        {palabras.map((p, i) => {
          const cp = cambios[p];
          return (
            <span key={i}>
              {cp ? cp : p}
              &nbsp;
            </span>
          );
        })}
      </div>
    );
  }
  return <div>{formatear()}</div>;
};

export default Formato;
