import { useFrame } from "@react-three/fiber";
import React, { useRef } from "react";
import * as THREE from "three";

const FichasModelos = ({ color, ficha3D, escena }) => {
  const fichaEscena = useRef();
  const mixer = useRef();
  ficha3D.materials.JColor.setValues({ color: new THREE.Color(color) });

  if (ficha3D.animations.length) {
    mixer.current = new THREE.AnimationMixer(ficha3D.scene);
    let clip = ficha3D.animations[escena];
    const action = mixer.current.clipAction(clip);
    action.setDuration(0.8);
    action.play();
    /* ESTO PERMITE COMBINAR TODAS LAS ANIMACIONES EN UNA SOLA EJECUCIN
    ficha3D.animations.forEach((clip) => {
      const action = mixer.current.clipAction(clip);
      action.setDuration(0.8);
      action.play();
    });
    */
  }

  useFrame((state, delta) => {
    fichaEscena.current.rotation.y += 0.002;
    mixer.current?.update(delta);
  });

  return (
    <primitive
      scale={[5, 5, 5]}
      position={[0, -20, 0]}
      ref={fichaEscena}
      object={ficha3D.scene}
    />
  );
};

export default FichasModelos;
