import React, { Suspense, useEffect, useRef, useState } from "react";
import {
  Button,
  Col,
  Container,
  Form,
  Row,
  Table,
  Image,
} from "react-bootstrap";
import { SelectFicha } from "./SelectFicha";
import Reglas from "./Reglas";

const GamePrep = ({
  partida,
  idJugador,
  hMensajes,
  sendAction,
  hMensajeErr,
  hSelectFicha,
  fichas,
  colores,
}) => {
  const [mensajes, setMensajes] = hMensajes;
  const [mensajeErr, setMensajeErr] = hMensajeErr;
  const [selectFicha, setSelectFicha] = hSelectFicha;
  const [verReglas, setVerReglas] = useState();

  const msjRef = useRef();
  const mensajesRef = useRef();

  function enviarTexto(e) {
    if (!msjRef.current.value) return;
    sendAction("message", msjRef.current.value);
    msjRef.current.value = "";
    msjRef.current.focus();
  }
  useEffect(() => {
    if (!mensajesRef.current) return;
    mensajesRef.current.scrollTop = mensajesRef.current.scrollHeight;
  }, [mensajes]);

  function handleConfigFicha() {
    setSelectFicha(true);
    console.log("cambiando ficha...");
  }

  function handleReglas() {
    setVerReglas(true);
    console.log("abriendo las reglas");
  }

  function handleChangeMaxPlayers(e) {
    const maxJugadores = e.target.value;
    if (maxJugadores < partida.jugadores.length) {
      e.target.value = partida.jugadores.length;
      setMensajeErr(
        "La cantidad de jugadores actuales excedera al mximo de jugadores"
      );
      return;
    }
    sendAction("setMaxPlayers", maxJugadores);
  }

  function handleQuitGame() {
    sendAction("quit", "Deseo salir");
    setMensajes("");
    console.log("saliendo del juego");
  }

  function handleAceptarReglas(reglasObj) {
    sendAction("setRules", reglasObj);
    console.log("Reglas enviadas");
  }

  function handleCheckListo(e) {
    const listo = e.target.checked;
    sendAction("setReady", listo);
  }

  let ja = partida.jugadores.find((j) => j.id == idJugador);
  let soloLectura = idJugador != partida.host;

  return selectFicha ? (
    <Suspense fallback={null}>
      <SelectFicha
        setShow={setSelectFicha}
        colorIni={ja.colorId}
        fichaIni={ja.ficha}
        sendAction={sendAction}
      />
    </Suspense>
  ) : verReglas ? (
    <Reglas
      rulesObj={partida.reglas}
      soloLectura={soloLectura}
      hVerReglas={[verReglas, setVerReglas]}
      onAceptar={handleAceptarReglas}
    />
  ) : (
    <Container style={{ width: "500px" }}>
      <Row>
        <h2>
          <Form.Label style={{ color: "aqua" }}>{partida.nombre}</Form.Label>
        </h2>
      </Row>
      <Container
        style={{
          height: 258,
          padding: 0,
          backgroundColor: "rgba(0, 0, 0, 0.5)",
          marginBottom: 10,
          border: "solid 1px white",
        }}
      >
        <Table variant="dark" size="sm">
          <thead>
            <tr style={{ fontSize: 14 }}>
              <th style={{ backgroundColor: "black" }}>Nombre</th>
              <th style={{ backgroundColor: "black" }}>Ficha</th>
              <th style={{ backgroundColor: "black" }}>Listo</th>
            </tr>
          </thead>
          <tbody>
            {partida.jugadores.map(function (j) {
              return (
                <tr
                  key={j.id}
                  className={j.id == idJugador ? "table-secondary" : ""}
                  style={{
                    verticalAlign: "middle",
                    fontSize: 14,
                  }}
                >
                  <td>{j.nombre}</td>
                  <td>
                    {j.id == idJugador ? (
                      <Button
                        size="sm"
                        onClick={handleConfigFicha}
                        style={{
                          backgroundColor: colores[j.colorId].valor,
                          borderColor: colores[j.colorId].valor,
                        }}
                      >
                        {j.ficha}
                      </Button>
                    ) : (
                      <div style={{ textAlign: "-webkit-center" }}>
                        <div
                          style={{
                            paddingBlock: "4px",
                            paddingInline: "8px",
                            backgroundColor: colores[j.colorId].valor,
                            borderRadius: "4px",
                            width: "fit-content",
                          }}
                        >
                          {j.ficha}
                        </div>
                      </div>
                    )}
                  </td>
                  <td>
                    <Form.Check
                      checked={j.listo}
                      disabled={j.id != idJugador}
                      onChange={handleCheckListo}
                    />
                  </td>
                </tr>
              );
            })}
          </tbody>
        </Table>
      </Container>
      <Row className="mb-4">
        <Col></Col>
        <Col xs={2}>
          <div
            style={{
              position: "relative",
              backgroundImage: "url('/imagenes/maxplayers.png')",
              width: "32px",
              height: "32px",
              filter: soloLectura
                ? null
                : "drop-shadow(2px 2px 1px rgba(0, 0, 0, 1))",
              overflow: "hidden",
            }}
          >
            {soloLectura ? (
              <Form.Label
                style={{
                  top: "12px",
                  color: "black",
                  fontWeight: "bold",
                  position: "absolute",
                  width: "32px",
                  left: "0px",
                }}
              >
                {partida.maxJugadores}
              </Form.Label>
            ) : (
              <Form.Select
                size="sm"
                style={{
                  position: "absolute",
                  backgroundColor: "transparent",
                  color: "black",
                  borderWidth: 0,
                  fontWeight: "bold",
                  width: "55px",
                  height: "32px",
                  marginLeft: "5px",
                  paddingTop: "12px",
                  cursor: "pointer",
                }}
                onChange={handleChangeMaxPlayers}
                disabled={soloLectura}
                defaultValue={partida.maxJugadores}
                title="Mximo jugadores"
              >
                <option value="2">2</option>
                <option value="3">3</option>
                <option value="4">4</option>
                <option value="5">5</option>
                <option value="6">6</option>
              </Form.Select>
            )}
          </div>
        </Col>
        <Col xs={2}>
          <Image
            src="/imagenes/rules1.png"
            title="Reglas"
            onClick={handleReglas}
            style={{
              cursor: "pointer",
              padding: 0,
              width: "32px",
              height: "32px",
              filter: "drop-shadow(2px 2px 1px rgba(0, 0, 0, 1))",
            }}
          />
        </Col>
        <Col xs={2}>
          <Image
            src="/imagenes/quitgame.png"
            title="Dejar partida"
            onClick={handleQuitGame}
            style={{
              cursor: "pointer",
              padding: 0,
              width: "32px",
              height: "32px",
              filter: "drop-shadow(2px 2px 1px rgba(0, 0, 0, 1))",
            }}
          />
        </Col>
        <Col></Col>
      </Row>
      <Container>
        <Row>
          <Form.Control
            as="textarea"
            rows={4}
            readOnly
            value={mensajes}
            ref={mensajesRef}
            size="sm"
          />
        </Row>
        <Row className="mt-3">
          <Col style={{ paddingInline: "0px" }}>
            <Form.Control
              type="text"
              placeholder="Escriba un mensaje..."
              ref={msjRef}
            />
          </Col>
          <Col xs={3}>
            <Button onClick={enviarTexto} variant="primary">
              Enviar
            </Button>
          </Col>
        </Row>
      </Container>
    </Container>
  );
};

export default GamePrep;
