import React, { useContext, useEffect, useRef, useState } from "react";
import { useLoader, useFrame } from "@react-three/fiber";
import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader";
import * as THREE from "three";
import FichasJugador from "./FichaJugador";
import Tablero from "./Tablero";
import * as SkeletonUtils from "three/examples/jsm/utils/SkeletonUtils";
import { GameContext } from "../contexts/GameContext";
import Dados from "./Dados";

const Fondo = ({ sentido = -1, selectFicha, orbitaRef }) => {
  console.log("renderizado Fondo.jsx");
  const { partida, fichas, colores, idJugador, egi, setEgi, sendAction } =
    useContext(GameContext);
  const mundo = useRef();
  const gFichas = useRef();
  const [fichaObjs, setFichasObjs] = useState([]);

  if (
    fichaObjs.length == 0 &&
    partida &&
    !["N", "I"].includes(partida.estado)
  ) {
    const fichasObjs_local = [];
    partida.jugadores.forEach((j) => {
      //cargar modelo y clonar
      const ruta = fichas.find((f) => f.nombre == j.ficha).ruta;
      const gltf = useLoader(GLTFLoader, ruta);
      const clon = SkeletonUtils.clone(gltf.scene);
      //asignar color: clonar material para controlar el color individualmente
      const JColor = clon.children.find((o) => o.name == "JColor");
      clon.children.forEach((o) => {
        if (o.isMesh) {
          o.castShadow = true;
          o.receiveShadow = true;
        }
      });
      JColor.material = new THREE.MeshStandardMaterial();
      JColor.material.copy(gltf.materials.JColor);
      JColor.material.color = new THREE.Color(colores[j.colorId].valor);
      //armar objeto personalizado
      const f3D = {
        idj: j.id,
        animations: gltf.animations,
        scene: clon,
        nombre: j.ficha,
      };
      fichasObjs_local.push(f3D);
    });
    setFichasObjs(fichasObjs_local);
  } else if (!partida && fichaObjs.length != 0) {
    setFichasObjs([]);
  }

  if (selectFicha) {
    // seleccionar ficha
    activarTransCam([0, 0, 0], [0, 15, 50]);
    console.log("TransCam: Seleccionar Ficha");
  } else if (!partida || partida.estado == "I") {
    // animacin inicial de fondo
    activarTransCam([0, 0, 0], [0, 100, 200]);
    console.log("TransCam: Preparacin");
  } else if (partida?.estado == "R") {
    orbitaRef.current.smoothTime = 0.7;
    activarTransCam([25, 0, -35], [-80, 110, 90], false);
    activarTransCam([25, 0, -35], [25, 80, -35]);
  } else if (["J", "G", "O"].includes(partida?.estado)) {
    centrarEnJugador();
  }
  if (["S", "D"].includes(partida?.estado)) {
    //Mostrar Vista Superior para seleccionar
    console.log("Fondo.Camara: Vista Superior para Seleccionar");
    activarTransCam([0, 0, 0], [0, 200, 10]);
  } else {
    console.log("Sin animacin de camara");
  }

  function centrarEnJugador() {
    const ja = partida.jugadores.find((j) => {
      return j.id == partida.jugadorActual;
    });
    if (ja) {
      let ft = ja.fichaTransform;
      const mt = new THREE.Matrix4();
      mt.fromArray(ft);
      let p = new THREE.Vector3();
      mt.decompose(p, new THREE.Quaternion(), new THREE.Vector3());
      activarTransCam([p.x, 0, p.z], [p.x, 50, 50 + p.z]);
    }
  }
  function handleSeleccionarFicha() {}
  function onCentrar(e) {
    activarTransCam([0, 0, 30], [0, 140, 130]);
  }
  let revMeta = 0;
  let nRev = 1;
  let rev1 = 2 * Math.PI;
  let revFalt = 0;
  let rfinal = 0;
  let t = 0;
  let aw = 0;
  let w1 = 0.002;
  useFrame(({ camera }) => {
    if (partida) {
      switch (partida.estado) {
        case "I": //Preparacin: girar constante
          //matener variables iniciales
          break;
        case "N": //Iniciando: acelerar
          if (t <= Math.PI / 2) {
            t += 0.01;
            w1 = Math.sin(t) / 10 + 0.002;
          }
          break;
        case "J": //Inicio de Turno: desacelerar y detener
          let roty = mundo.current.rotation.y;
          if (egi == 0 && revMeta == 0) {
            w1 = 0.102;
            revFalt = nRev * rev1 + (rev1 - (Math.abs(roty) % rev1));
            revMeta = roty + revFalt * sentido;
            rfinal = Math.abs(revMeta - roty);
            aw = Math.pow(w1, 2) / (2 * revFalt);
            activarTransCam([0, 0, 0], [0, 90, 120]);
            console.log("punto2");
          } else {
            if (rfinal <= 0.005) {
              w1 = 0;
              if (egi != 2) {
                mundo.current.rotation.y = 0;
                rfinal = 0;
                setEgi(2);
                activarTransCam([0, 0, 0], [0, 90, 120], true);
                console.log("punto3");
              }
            } else {
              rfinal = Math.abs(revMeta - roty);
              w1 -= aw;
            }
          }
          break;
        default:
          w1 = 0;
        //activarTransCam([0, 0, 0], [0, 90, 120]);
        //console.log("punto4");
      }
    }

    mundo.current.rotation.y += sentido * w1;
  });

  function activarTransCam(vC, vE, soft = true) {
    if (orbitaRef.current) {
      orbitaRef.current.setTarget(vC[0], vC[1], vC[2], soft);
      orbitaRef.current.setPosition(vE[0], vE[1], vE[2], soft);
    }
  }

  function handlePointerDown(e) {
    //activarTransCam([0, -60, 0], [0, 90, 120]);
  }
  function handleRollingEnd() {
    orbitaRef.current.smoothTime = 0.3;
    centrarEnJugador();
  }

  return (
    <group ref={mundo}>
      <Tablero
        partida={partida}
        sendAction={sendAction}
        idJugador={idJugador}
        onDoubleCick={onCentrar}
      />
      {["J", "R"].includes(partida?.estado) && (
        <Dados onRollingEnd={handleRollingEnd} />
      )}
      <group ref={gFichas} onPointerDown={handlePointerDown}>
        {partida &&
          !["N", "I"].includes(partida.estado) &&
          partida.jugadores.map((j) => {
            if (j.bancaRota) return null;
            const fItem = fichaObjs.find((f) => f.idj == j.id);
            return (
              fItem && (
                <FichasJugador
                  key={`F${j.id}`}
                  ficha3D={fItem}
                  fichaEstado={j.fichaEstado}
                  fichaTransform={j.fichaTransform}
                  orbitaRef={orbitaRef}
                  onSeleccionar={handleSeleccionarFicha}
                />
              )
            );
          })}
      </group>
    </group>
  );
};

export default Fondo;
