import React from "react";
import { Button, Container, Row } from "react-bootstrap";
import Formato from "../tools/Formato";

const DialogoAviso = ({
  partida,
  manage,
  idd,
  mensaje,
  sendAction,
  ok = true,
}) => {
  function handleAceptar() {
    sendAction("closeDialog", { idg: idd });
  }
  return (
    <Container
      style={{
        pointerEvents: "all",
        height: "fit-content",
        backgroundColor: "rgba(50, 50, 50, 0.5)",
        padding: "15px",
        borderRadius: "10px",
        backdropFilter: "blur(5px)",
      }}
    >
      <Row>
        <Formato partida={partida} mensaje={mensaje} />
      </Row>
      <Row className="my-1" style={{ justifyContent: "center" }}>
        {manage && ok && (
          <Button
            size="sm"
            onClick={handleAceptar}
            style={{ width: "fit-content" }}
          >
            OK
          </Button>
        )}
      </Row>
    </Container>
  );
};

export default DialogoAviso;
