import React from "react";
import { Button, Container, Image, Row, Col, Stack } from "react-bootstrap";
import { moneda } from "../misc/CalcUtils";

const DialogoCompra = ({ manage, solvente, data, sendAction }) => {
  function handleComprar() {
    sendAction("closeDialog", { idg: data.id, rc: 1 });
  }
  function handleNo() {
    sendAction("closeDialog", { idg: data.id, rc: 0 });
  }
  return (
    <Container
      style={{
        pointerEvents: "all",
        justifyContent: "center",
        width: "230px",
        height: "fit-content",
        backgroundColor: "rgba(10, 10, 10, 0.5)",
        padding: "15px",
        borderRadius: "10px",
        backdropFilter: "blur(5px)",
      }}
    >
      <Row>
        <Image src={`/imagenes/t${data.contenido.id}.jpg`}></Image>
      </Row>
      <Row className="my-1">
        {manage && (
          <Stack direction="horizontal" gap={1}>
            <Button
              size="sm"
              onClick={handleComprar}
              disabled={!solvente}
              variant={solvente ? "primary" : "secondary"}
            >
              {`Comprar por ${moneda(data.contenido.precio)}`}
            </Button>
            <Button variant="danger" size="sm" onClick={handleNo}>
              No, gracias
            </Button>
          </Stack>
        )}
      </Row>
    </Container>
  );
};

export default DialogoCompra;
